export function generateToast(tone, title, message) {
  let markup = `
    <main class="toast-wrapper">
      <div class="toast toast--${tone}">
        <header class="toast-header">
          <h3 class="heading4">${title}</h3>
          <svg class="toast-close" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24" role="img" aria-label="Clear Search" focusable="false" style="display: block; fill: currentcolor;"><path d="m23.25 24c-.19 0-.38-.07-.53-.22l-10.72-10.72-10.72 10.72c-.29.29-.77.29-1.06 0s-.29-.77 0-1.06l10.72-10.72-10.72-10.72c-.29-.29-.29-.77 0-1.06s.77-.29 1.06 0l10.72 10.72 10.72-10.72c.29-.29.77-.29 1.06 0s .29.77 0 1.06l-10.72 10.72 10.72 10.72c.29.29.29.77 0 1.06-.15.15-.34.22-.53.22" fill-rule="evenodd"></path></svg>
        </header>
        <p class="medium">${message}</p>
      </div>
    </main>
  `
  $('body').bind('append', showToast)
  $('body').append(markup)
  $('body').unbind('append')
}

export function showToast() {
  let toast = $('.toast-wrapper')
  toast.delay(3500).fadeOut(360)
}

$(document).on('click', '.toast-close', function(e) {
  let toast = $('.toast')
  toast.fadeOut(360)
})
